/* Card de proyectos con efectos mejorados */
.project-card-enhanced {
  position: relative;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.9), rgba(248, 250, 252, 0.9));
  backdrop-filter: blur(10px);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.project-card-enhanced:hover {
  transform: translateY(-2px);
  box-shadow: 
    0 20px 25px -5px rgba(0, 0, 0, 0.1),
    0 10px 10px -5px rgba(0, 0, 0, 0.04),
    inset -2px 0 4px rgba(0, 0, 0, 0.05);
}

/* Efectos específicos por color */
.project-card-slate {
  box-shadow: 
    0 4px 6px -1px rgba(71, 85, 105, 0.1),
    0 2px 4px -1px rgba(71, 85, 105, 0.06),
    inset -1px 0 2px rgba(71, 85, 105, 0.1);
}

.project-card-slate:hover {
  box-shadow: 
    0 20px 25px -5px rgba(71, 85, 105, 0.15),
    0 10px 10px -5px rgba(71, 85, 105, 0.08),
    inset -3px 0 6px rgba(71, 85, 105, 0.1);
}

.project-card-blue {
  box-shadow: 
    0 4px 6px -1px rgba(59, 130, 246, 0.1),
    0 2px 4px -1px rgba(59, 130, 246, 0.06),
    inset -1px 0 2px rgba(59, 130, 246, 0.1);
}

.project-card-blue:hover {
  box-shadow: 
    0 20px 25px -5px rgba(59, 130, 246, 0.15),
    0 10px 10px -5px rgba(59, 130, 246, 0.08),
    inset -3px 0 6px rgba(59, 130, 246, 0.1);
}

.project-card-emerald {
  box-shadow: 
    0 4px 6px -1px rgba(16, 185, 129, 0.1),
    0 2px 4px -1px rgba(16, 185, 129, 0.06),
    inset -1px 0 2px rgba(16, 185, 129, 0.1);
}

.project-card-emerald:hover {
  box-shadow: 
    0 20px 25px -5px rgba(16, 185, 129, 0.15),
    0 10px 10px -5px rgba(16, 185, 129, 0.08),
    inset -3px 0 6px rgba(16, 185, 129, 0.1);
}

/* Card de educación con efectos mejorados */
.education-card-enhanced {
  position: relative;
  background: linear-gradient(135deg, rgba(239, 246, 255, 0.9), rgba(255, 255, 255, 0.9));
  box-shadow: 
    0 4px 6px -1px rgba(59, 130, 246, 0.1),
    0 2px 4px -1px rgba(59, 130, 246, 0.06),
    inset -1px 0 2px rgba(59, 130, 246, 0.08);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.education-card-enhanced:hover {
  transform: translateY(-1px);
  box-shadow: 
    0 20px 25px -5px rgba(59, 130, 246, 0.12),
    0 10px 10px -5px rgba(59, 130, 246, 0.06),
    inset -3px 0 6px rgba(59, 130, 246, 0.08);
}

/* Certificación card con efectos mejorados */
.certificate-card-enhanced {
  position: relative;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.95), rgba(247, 254, 231, 0.95));
  box-shadow: 
    0 2px 4px -1px rgba(16, 185, 129, 0.08),
    0 1px 2px -1px rgba(16, 185, 129, 0.04),
    inset -1px 0 1px rgba(16, 185, 129, 0.06);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.certificate-card-enhanced:hover {
  transform: translateX(2px);
  box-shadow: 
    0 8px 12px -3px rgba(16, 185, 129, 0.1),
    0 4px 6px -2px rgba(16, 185, 129, 0.05),
    inset -2px 0 4px rgba(16, 185, 129, 0.06);
}

/* Asegurar que los elementos de certificación no se desborden */
.certificate-container {
    width: 100%;
    max-width: 100%;
    overflow: hidden;
}

.certificate-buttons {
    flex-shrink: 0;
    min-width: fit-content;
}

/* Responsive improvements for cards */
@media (max-width: 640px) {
    .project-card-enhanced,
    .education-card-enhanced {
        margin: 0 -0.5rem;
    }
    
    .certificate-card-enhanced {
        border-radius: 0.375rem;
        margin: 0 !important;
    }
    
    /* Asegurar que las cards no se salgan */
    [class*="Card"], .card {
        width: 100% !important;
        max-width: 100% !important;
        margin-left: 0 !important;
        margin-right: 0 !important;
        box-sizing: border-box !important;
    }
}